// .main {}
.main {
	padding-inline: 50px;
	.title {
		color: var(--sPrimary);
		font-size: 26px;
		font-weight: 700;
		font-style: normal;
		line-height: normal;
		text-align: center;
		margin-bottom: 30px;
	}

	.map {
		width: 100%;
		height: 121px;
	}

	.item {
		margin-top: 15px;
		margin-bottom: 15px;
		gap: 10px;
		display: flex;
		flex-direction: column;

		&Title {
			font-size: 14px;
			font-weight: 700;
			font-style: normal;
			line-height: normal;
			color: var(--sPrimary);
		}
	}
}
